import React,  {Component} from 'react';

import './About.css';

class About extends Component {

    render() {
      return (
        <div id="index-banner" className="parallax-container about-container">
          <div className="container">
            <div className="row"> 
              <div className="col m12 center-align">
              <div className="col m4 offset-s2 center-align">
                  <img className="circle prof-pic" src={require("../images/me.jpg")} alt=""/>
              </div>
                  <div className="col m6 offset-s1">
                    <br></br><br></br><br></br><br></br>
                    <span className="about-text">
                        Hi, my name is Christian Gingich.
                        I'm a Web developer able to build a sophisticated website with full database backend all the way from mockup to rollout. Adept at working with diverse teams to implement projects. A quick study on new technologies.</span>
                  </div>
                </div> 
        </div>
        </div>
        </div> 
)
    }
}

export default About;


