import React,  {Component} from 'react';
import projectsone from './projects1.json';
import projectstwo from './projects2.json';
import './SinglePage.css';

class SinglePage extends Component {
    constructor(props){
        super(props);
        this.state = {
        projectsone: projectsone,
        projectstwo: projectstwo,
        }
        };

    render() {
      return (
        <div>
           {/* navbar */}
         <nav className="navbar" >
        <div className="nav-wrapper container">
        <div className="row center">
        <div className="col sm12 m3">
            <a href="/">Home</a>
        </div>
        <div className="col sm12 m3">
            <a href="/about">About</a>
        </div>
        <div className="col sm12 m3">
            <a href="/portfolio">Portfolio</a>
        </div>
        <div className="col sm12 m3">
            <a href="/contact">Contact</a>
        </div>
        </div>
        </div>
        </nav>

          {/* about */}
        <div id="index-banner" className="parallax-container about-container">
          <div className="container">
            <div className="row"> 
              <div className="col m12 center-align">
              <div className="col m4 offset-s2 center-align">
                  <img className="circle prof-pic" src={require("../images/me.jpg")} alt=""/>
              </div>
                  <div className="col m6 offset-s1">
                    <br></br><br></br><br></br><br></br>
                    <span className="about-text">
                        Web developer able to build a sophisticated website with full database backend all the way from mockup to rollout. Adept at working with diverse teams to implement projects. A quick study on new technologies.  </span>
                  </div>
                </div> 
        </div>
        </div>
        </div> 
        {/* projects */}
      







</div>
)
    }
}

export default SinglePage;


